import React, { Component, PropTypes } from 'react'

export default class Header extends Component {
    render() {
        return <div>
            <header id="fh5co-header-section" role="header" className="" >
                <div className="container">
                    <h1 id="fh5co-logo" className="pull-left">
                        <a href="index.html">
                            <img src="dist/images/logo.png" alt="Slant Free HTML5 Template" />
                        </a>
                    </h1>					
                    <nav id="fh5co-menu-wrap" role="navigation">
                        <ul className="sf-menu" id="fh5co-primary-menu">
                            <li className="active">
                                <a href="index.html">EasyWeb</a>
                            </li>
                            <li className="fh5co-special"><a href="contact.html">Войти или зарегистрироваться</a></li>
                        </ul>
                    </nav>
                </div>
            </header>
        </div>
    }
}


